@mixin clearfix {
  &:after {
    content: ".";
    clear: both;
    display: block;
    visibility: hidden;
    height: 0;
  }
}

@mixin nucleo {
  display: inline-block;
  font-size: inherit;
  speak: none;
  text-transform: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@mixin nucleo_outline {
  font-family: "Nucleo Outline";

  @include nucleo;
}

@mixin nucleo_mini {
  font-family: "Nucleo Mini";

  @include nucleo;
}

@mixin nucleo_glyph {
  font-family: "Nucleo Glyph";

  @include nucleo;
}

@mixin fontawesome {
  display: inline-block;
  font: normal normal normal 14px/1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@mixin sprites {
  background-image: url(../images/sprites.png);
  background-repeat: no-repeat;
}

@mixin border-radius($number...) {
  border-radius: $number;
  -webkit-border-radius: $number;
}

@mixin box-shadow($val...) {
  box-shadow: $val;
  -webkit-box-shadow: $val;
}

@mixin text-shadow($val...) {
  text-shadow: $val;
  -webkit-text-shadow: $val;
}

@mixin transition($val...) {
  -webkit-transition: $val;
  -moz-transition: $val;
  -o-transition: $val;
  transition: $val;
}

@mixin opacity($opacity) {
  opacity: $opacity;

  // IE8 filter
  $opacity-ie: $opacity * 100;

  filter: #{alpha(opacity = $opacity-ie)};
}

@mixin translate($val...) {
  -webkit-transform: translate($val);
  -moz-transform: translate($val);
  -o-transform: translate($val);
  transform: translate($val);
}

@mixin scale($val...) {
  -webkit-transform: scale($val);
  -moz-transform: scale($val);
  -o-transform: scale($val);
  transform: scale($val);
}

@mixin rotate($val) {
  -ms-transform: rotate($val);

  /* IE 9 */
  -webkit-transform: rotate($val);

  /* Chrome, Safari, Opera */
  transform: rotate($val);
}

@mixin transform-origin($val) {
  -webkit-transform-origin: inherit;
  -moz-transform-origin: inherit;
  -ms-transform-origin: inherit;
  -o-transform-origin: inherit;
  transform-origin: inherit;
}

@mixin animation($val) {
  -webkit-animation: $val;
  -moz-animation: $val;
  -o-animation: $val;
  animation: $val;
}

/* Placeholder */
@mixin placeholder($color) {
  &::-webkit-input-placeholder {
    color: $color;
  }

  &:-moz-placeholder {
    /* older Firefox*/
    color: $color;
  }

  &::-moz-placeholder {
    /* Firefox 19+ */
    color: $color;
  }

  &:-ms-input-placeholder {
    color: $color;
  }

  &:-moz-placeholder {
    /* Mozilla Firefox 4 to 18 */
    color: $color;
    opacity: 1;
  }

  &::-moz-placeholder {
    /* Mozilla Firefox 19+ */
    color: $color;
    opacity: 1;
  }
}

@mixin page_gold_border($opacity) {
  content: " ";
  width: 48px;
  position: absolute;
  left: 0; /* LTR */
  margin-left: -194px; /* LTR */
  opacity: $opacity;
  background-image: -webkit-linear-gradient(-90deg, #a88231 0%, #e9d16c 100%);
  background-image: -moz-linear-gradient(-90deg, #a88231 0%, #e9d16c 100%);
  background-image: -o-linear-gradient(-90deg, #a88231 0%, #e9d16c 100%);
  background-image: linear-gradient(-90deg, #a88231 0%, #e9d16c 100%);
}

@mixin page_goldbar($opacity) { 
  opacity: $opacity;
  background-image: -webkit-linear-gradient(-90deg, #a88231 0%, #e9d16c 100%);
  background-image: -moz-linear-gradient(-90deg, #a88231 0%, #e9d16c 100%);
  background-image: -o-linear-gradient(-90deg, #a88231 0%, #e9d16c 100%);
  background-image: linear-gradient(-90deg, #a88231 0%, #e9d16c 100%);
}
