//tablet
@media (min-width: 992px) and (max-width: 1199px) {
  .page .sidebar #user-login-form .form-actions{
    padding-left: 0;
    padding-right: 0;
  }

}
@media (max-width: 1169px) and (min-width: 992px) {
  #footer {
    .menu {
      li {
        margin-left: 6px;
        padding-left: 6px;
      }
    }
  }


  .views-field-field-images {
    a {
      img {
        max-width: 91px;
      }
    }
  }
}

@media (max-width: 1100px) {
  .goldbar {
    display: none;
  }
}

@media (max-width: 991px) {
  .container {
    width: auto;
    padding-left: 20px;
    padding-right: 20px;
  }

  .btn-btt {
    display: none;
  }

  /* Gold Bars */
  .goldbar{
    display: none;
  }


  #featured .slick-slide img{
    width: 100%;
  }

  #menu-toggle{
    display: none !important;
  }

  .navbar-we-mega-menu {
    display: none;
  }

  .navbar-we-mega-menu.mobile-main-menu {
    display: block;
  }
  #main-menu-inner{
    display: block;
  }

  /* nav */
  #page {
    padding-left: 0;
    right: 0;
    transition: all 0.5s ease-in-out;
    -webkit-transition: all 0.5s ease-in-out;
    -moz-transition: all 0.5s ease-in-out;
    -o-transition: all 0.5s ease-in-out;
    position: relative;
    margin-right: 0;
  }
  #page.toggled {
    right: 250px;
  }

  .btn-close {
    transition: all 0.3s ease-in-out;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    display: block;
    color: white;
    cursor: pointer;
    position: absolute;
    padding: 5px 10px;
    right: auto;
    left: 20px;
    /*top: 30px;*/
    font-size: 12px;
    color: #CCC;
    box-shadow: none;
    background: none;

    &:before, &:after {
      background-color: #AAA;
    }
  }
  .btn-close:hover, .btn-close:focus {
    color: white;
  }
  #page.toggled .mobile-main-menu {
    padding-top: 74px;
    right: 0;
    display: block;
  }
  .overlay {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 999;
    background-color: rgba(0, 0, 0, 0.5);
    width: 100%;
    height: 100%;
  }
  /* main-menu */
  #main-menu.mobile-main-menu {
    position: fixed;
    top: 0;
    bottom: 0;
    right: -250px;
    width: 250px;
    height: 100%;
    z-index: 1000;
    overflow-y: auto !important;
    background: #333;
    transition: all 0.5s ease-in-out;
    -webkit-transition: all 0.5s ease-in-out;
    -moz-transition: all 0.5s ease-in-out;
    -o-transition: all 0.5s ease-in-out;
    display: block !important;
    padding: 30px 0 0;
    max-height: none;
    ul.menu > li{
      margin-left: 0;
      margin-bottom: 0;
      padding-top: 0;
      padding-right: 0;
      list-style: none;
      float: none;
      display: block;
      > a {
        border-top: 1px solid rgba($black, 0.3);
        box-shadow: 0 1px 0 rgba($white, 0.05) inset;
        padding: 15px;
        background: none;
        &.active, &.active-trail {
          color: #fff !important;
          background-color: #1a1a1a;
        }
        &.active-trail {
          color: $white;
        }
        &:hover, &:focus {
          &:before {
            border-top-color: $white;
          }
        }
      }
      .is-active{
        background-color: #1a1a1a;
        color: #fff;
      }
    }
    .btn-close{
      display: block;
    } 
  }
  #header {
    .navbar-toggle {
      float: right;
      border: none;
      border-radius: 0;
      padding: 0;
      width: 40px;
      height: 29px;
      text-align: center;
      display: block;
      .icon-bar {
        background-color: $dark_grey;
        margin-left: auto;
        margin-right: auto;
      }
      &:hover, &:focus {
        background-color: #e0dfdf;
      }
    }
    a.navbar-toggle {
      margin: 20px 0 0;
    }
    .collapsed {
    }
  }

  .navbar-toggle .icon-bar + .icon-bar{
    margin-top: 3px;
  }

  /* Mega mobile menu */
  .navbar-we-mega-menu.navbar {
    .nav .open{
      >a{
        &:focus, &:hover {
          color: $menu_color !important; 
          background-color: transparent;
          cursor: default;
          border: 0 none;
          -moz-box-shadow: 0 1px 0 rgba(255,255,255,0.05) inset;
          -webkit-box-shadow: 0 1px 0 rgba(255,255,255,0.05) inset;
          box-shadow: 0 1px 0 rgba(255,255,255,0.05) inset;
          border-top: 1px solid rgba(0,0,0,0.3);
        }
      }   
    } 
    ul a{
      color: $menu_color;
    }
    ul li{
      clear: both;
    }
  }


  .navbar-we-mega-menu.navbar {
    background-color: #FFFFFF;
  }

  .navbar-we-mega-menu.navbar ul {
    width: 100%;
    padding: 0 10px;
  }

  .navbar-we-mega-menu.navbar .we-mega-menu-ul>li>a{
    color: #9b9b9b;
    text-shadow: none;
    -moz-box-shadow: none;
    -webkit-box-shadow: none;
    box-shadow: none;
    font-weight: bold;
    border-top-color: #EEEEEE;
  }

  .navbar-we-mega-menu.navbar .nav-tabs>li.active>a, 
  .navbar-we-mega-menu.navbar .nav-tabs>li.active>a:focus, 
  .navbar-we-mega-menu.navbar .nav-tabs>li.active>a:hover {
    background-color: #FFFFFF;
    color: $gold !important;
    border-top-color: $gold;
  }

  /* slideshow */
  #search-api-page-block-form,
  #search-api-page-block-form--2 {
    position: absolute;
    right: 15px;
    top: 15px;
    .form-search {
      width: 0px;
    }
    &:before {
      float: right;
    }
    &:hover, &:focus {
      width: 70%;

      .form-search {
        width: auto;
      }
    }
  }
  .block-language ul.links li a {
    padding: 0px;
  }

  .logged-in {
    #search-block-form {
      right: 47px;
      top: 21px;
    }
  }

  /* main menu */
  #main-menu {
    max-height: 20000px;
    margin: 0 -20px;
    clear: both;
    float: none;
    padding: 0;
    position: static;

    .container-fluid {
      padding: 0;
    }

    .container {
      width: 100%;
      height: auto;

      .block {
        float: none;
      }
    }

    .navbar-header {
      position: absolute;
      right: 20px;
      top: 38px;
      z-index: 500;
    }

    .navbar-toggle {
      display: block;
      background: #ddd;
      margin: 0;
      border-radius: 3px;
      -webkit-border-radius: 3px;
      padding: 7px 8px;

      &:hover, &:focus {
        background: $light_grey;
      }

      .icon-bar {
        background: #333;
      }
    }

    .navbar-collapse {
      max-height: 200000px;

      .container {
        height: 0;
        margin: 0;
        overflow: hidden;
        padding-left: 20px;
        padding-right: 20px;

        ul.menu {
          float: none;
          padding: 5px 15px;
          margin: 15px 0 0;
          background: #444;

          &:before {
            border-color: transparent transparent #444 transparent;
            border-style: solid;
            border-width: 0 15px 15px;
            content: "";
            position: absolute;
            top: 0;
            z-index: 10;
            right: 25px;
          }

          li {
            border-left: none;
            float: none;
            border-top: $grey;
            border-top-style: dotted;

            a {
              display: block;
              color: $white;
              border-radius: 0;
              padding: 10px 0;
              text-shadow: none;
            }

            .active {
              color: $red;
              background: none;
            }

            &:hover, &:focus {
              a {
                background: none;
                color: $red;
              }
            }

            &:first-child {
              border-top: none;
            }

            ul.menu {
              position: static;
              left: auto;
              top: auto;
              width: auto;
              display: block;
              border: none;
              background: none;
              margin: 0;

              &:before {
                display: none;
              }

              li {
                border-top: $grey;
                border-top-style: dotted;

                a {
                  background: none;
                  color: $white;
                  padding: 10px 0;

                  &:hover, &:focus {
                    color: $red;
                  }
                }
              }
            }
          }

          .expanded {
            > a:before {
              color: $white;
            }
          }
        }
      }
    }

    .navbar-collapse.in {
      .container {
        height: auto;
        overflow: visible;
      }
    }
  }

  .page {
    #main {
      padding-bottom: 0;
    }
  }

  #panel_second {
    .col-xs-12 {
      margin-bottom: 30px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  #footer {
    .container {
      .block {
        width: 100%;
        float: none;
        text-align: center;
      }

      ul.menu {
        float: none;
        padding-top: 5px;
        text-align: center;

        li {
          float: none;
          display: inline-block;
          padding: 0 6px;
          margin: 10px 5px;
        }
      }
    }
  }

  .sidebar {
    margin-top: 20px;
  }

  .teaser{
    .view-content {
      .node--view-mode-teaser{
        .group-header{
          display: none;
        }
        .group-mobile{
          display: block;
        }
      }
    }
  }

  /*  .view-taxonomy-term {
      .view-content {
        .node::first-child {
          .group-header{
            display: block;
          }
        }
      }
    }*/

  /*  .block-views-blockrelated-articles-block-1 .view-content .field--name-node-title:after, 
    .block-views-blockrelated-articles-block-1 .view-content .field--name-node-title:before, 
    .view-taxonomy-term .view-content .field--name-node-title:after, 
    .view-taxonomy-term .view-content .field--name-node-title:before, 
    .view-women-profiles .view-content .field--name-node-title:after, 
    .view-women-profiles .view-content .field--name-node-title:before*/

  .view-taxonomy-term {
    .view-content {
      h2,
      .field--name-node-title {
        width: auto;
        text-align: initial;
        &:before, &:after {
          content: none;
        }

      }
    }
  }
  .block-views-blockauthor-articles-block-1, 
  .block-views-blockrelated-articles-block-1, 
  .block-views-blockduplicate-of-related-articles-block-1  {
    h2 {
      text-align: initial;
      &:before, &:after {
        content: none;
      }
    }
  }
}

