  blockquote {
    border: none;
    position: relative;
    padding: 30px 0;
    p {
      text-align: center;
      &:first-child {
        width: 100%;
        margin: 0 auto;
        position: relative;
        padding: 0 30px;
        font-size: 30px !important;
        color: $grey_font_header;
      }

      &:last-child {
        font-size: 18px;
      }
    }
    &:after, &:before {
      content: " ";
      position: absolute;
      border: 1px solid $gold;
      width: 25%;
      height: 1px;
    }
    &:before {
      top: 0;
      right: 35%;
    }

    &:after {
      right: 35%;
      bottom: 0;
    }
  }

  /* Block Related Articles */
  .block-views-blockrelated-articles-block-1,
  .block-views-blockduplicate-of-related-articles-block-1 {
    h2 {
      font-size: 26px;
      &:after, &:before {
        width: 15%;
      }
    }
  }

  /* Teaser */
  .article-teaser,
  .teaser {
    margin-bottom: 20px;

    .field--name-field-biography,
    .field--name-body {
      height: auto !important;

      p { 
        font-size: 16px !important;
        margin-bottom: 0px;
      }
    }
  }

  /* Woman Profile */
  .page-node-type-woman-profile,
  .view-women-profiles {

    .layout__region--bottom:before {
      content: none;
    }

    .field--name-node-title {
      h1 {
        margin-top: 0;
        margin-bottom: 0;
      }
    }
    .field--name-field-subtitle {
      margin-bottom: 5px;
    }

  }

  .view-filters {
    margin: 0 auto;
    text-align: center;

    label {
      width: 50px;
      text-align: left;
    }
    .form-actions{
      text-align: center !important;
    }
  }

  .more-link {
    margin-bottom: 10px;
  }

  .mailchimp-signup-subscribe-form,
  .contact-message-feedback-form {
    padding: 0;
    border: 0;

    .form-item {

      label {
