.container {
  padding-left: 0;
  padding-right: 0;
  position: relative;
}
.feed-icons {
  display: none;
}
/* view */
.views-field, .field {
  margin-bottom: 5px;
}
.view-filters .form-actions{
  clear: none;
}
.field-type-image {
  margin-bottom: 15px;
}

.page {
  #main {
    padding-top: 30px;
    padding-bottom: 30px;

    .content {
      .node {
        /*        .content {
                  margin-bottom: 30px;
                  padding: 30px;
                  position: relative;
                  background: $dark;
        
                  &:before {
                    border-color: transparent transparent $dark transparent;
                    border-style: solid;
                    border-width: 0 15px 15px;
                    content: "";
                    position: absolute;
                    top: -15px;
                    z-index: 10;
                    left: 30px;
                  }
                }*/

        /*        h2 {
                  margin: -40px 0 30px;
                  padding-right: 130px;
                  line-height: 1.4;
                }*/

        .date-wrapper {
          display: block;
          margin: 0 0 15px;
          overflow: hidden;
          padding: 0;
          width: 100%;

          .date-content {
            display: block;
            float: right;
            height: 53px;
            overflow: hidden;
            padding: 9px 0 0;
            width: 103px;

            span {
              color: $white;
            }
          }

          .date-left {
            display: block;
            float: left;
            font-family: 'oswaldregular';
            font-size: 2em;
            line-height: 1.2;
            text-align: center;
            width: 49px;
          }

          .date-right {
            border-left: 1px solid #999;
            display: block;
            float: left;
            line-height: 1.2;
            text-align: center;
            text-transform: uppercase;
            width: 53px;
          }
        }
      }

      .gallery-list {
        .views-row {
          margin-bottom: 30px;
        }

        .views-col {
          margin-right: 0;
        }

        .views-field-field-images {
          margin: 0 15px;
        }

        .views-field-title {
          background: #333;
          border: 1px solid #444;
          padding: 10px;
          margin-left: 15px;
          margin-right: 15px;

          a {
            color: $light_grey;
            font-size: 1.142em;

            &:hover, &:focus {
              color: $red;
            }
          }
        }
      }

      .content {
        .user-login-form, .user-pass, .user-register-form {
          padding: 30px;

          .form-item {
            margin: 0 0 10px;

            label {
              min-width: 100px;
              margin-right: 5px;
            }

            .form-text, .form-email, .form-select {
              border: 1px solid #2E2E2E;
              border-radius: 4px;
            }
          }

          input, button, select, textarea, details {
            border-color: #2E2E2E;
          }
        }

        .user-login-form .description {
          padding: 5px 0 5px 105px;
        }
      }
    }

    .views-field-created, .submitted {
      color: $grey;
      font-size: 0.921em;
    }

    .field-name-field-tags {
      .field-items {
        display: inline;
        float: none;

        .field-item {
          display: inline;
        }
      }
    }

    .pager {
      li {
        display: inline-block;
        margin: 0 1px;
        padding: 0;

        a {
          background-color: #555;
          border-color: #000;
          color: #ccc;
          display: inline-block;
          padding: 5px 14px;
          border-radius: 0;

          &:hover, &:focus {
            background: $white;
            color: $red;
          }
        }

        &.is-active {
          a {
            background: $white;
            color: $red;
          }
        }
      }
    }

    .action-links {
      margin-top: 0;
    }

    /* forum */
    #forum {
      table {
        width: 100%;
        margin-bottom: 20px;

        th {
          padding: 10px 7px;
          background: $white;
          color: $gold;
          border: 1px solid #aaa;
        }

        td {
          padding: 7px;
          background: $light_grey;
          color: $gold;
          border: 1px solid #aaa;
          position: static;

          .submitted {
            color: #666;
          }
        }

        a {
          &:hover {
            color: $gold;
          }
        }
      }

      td.created, td.posts, td.topics, td.last-reply, td.replies, td.pager {
        white-space: normal;
      }
    }

    .field-name-taxonomy-forums {
      margin-bottom: 15px;

      .field-label {
        float: left;
        margin-right: 5px;
      }

      .field-items {
        display: inline;

        .field-item {
          display: inline;
        }
      }
    }

    /* comment */
    .field--name-comment {
      margin-bottom: 40px;

      .comment-form-block {
        padding: 20px 60px;
        border: 1px solid $grey_font_header;
        border-radius: 4px;

        h2 {
          font-size: 20px;
          color: #4C4C4C;
        }
      }

      h2.title:not(.comment-form__title) {
        font-size: 16px;
        color: #4c4c4c;
        border-bottom: 5px solid #e5e4e3;
        padding-bottom: 4px;
        margin-top: 53px;

        &:before {
          content: "\f104";
          font-family: 'Ionicons';
          font-size: 20px;
          color: $gold;
          margin: 10px;
        }

        &:after {
          content: " ";
          border-bottom: 5px solid #B69235;
          width: 215px;
          height: 5px;
          display: block;
          padding-top: 4px;
          position: absolute;
        }
      }

      h2.comment-form__title {
        font-size: 24px;
        text-align: center;
        &:after {
          content: " ";
          border-bottom: 1px solid #D3D1CC;
          width: 7%;
          display: block;
          margin: 0 auto;
          margin-top: 20px;
        }
      }

      .field--name-username,
      .field--name-user-picture {
        display: none;
      }

      .field--name-field-name,
      .field--name-comment-author,
      .field--name-comment-post-date,
      .form-item-name,
      .field--name-field-comment-body {
        font-size: 18px;
        color: #555555;
        margin-left: 40px; /* ltr */
      }
      .field--name-field-name,
      .field--name-comment-author {
        float: left; /* ltr */
        color: $grey_font_header;
        margin-bottom: 9px;
      }
      .field--name-field-description {display: none;}
      .field--name-comment-post-date {
        float: right; /* ltr */
        color: $grey_font_header;
      }
      .field--name-field-comment-body {
        clear: both;
        border-bottom: 1px solid #dddddd;
        padding-bottom: 20px;
        margin-bottom: 20px;
      }
    }

    #comments {
      padding-top: 30px;
      margin-top: 30px;
      border-top: 1px dotted #444;

      h2 {
        text-transform: uppercase;
        margin: 0 0 20px !important;
        padding-right: 0 !important;
      }

      #comment-form {
        padding: 0;
        background: none;
        margin: 0;
      }

      mark {
        float: right;
        margin: 0 0 10px 10px;
        padding: 1px 5px;
      }
    }

    .comment {
      .comment__meta{
        display: none;
      }
      margin-bottom: 30px;
      padding: 20px;

      h3 {
        font-size: 1.28em;
        font-family: arial;
        font-weight: bold;
        margin-bottom: 10px;
      }

      .content {
        background: none !important;
        padding: 0 !important;
        margin-bottom: 20px !important;

        &:before {
          display: none !important;
        }
      }

      ul.links {
        margin-bottom: 0;

        li {
          a {
            display: inline-block;
            padding: 5px 15px;
            background: $white;
            font-size: 0.921em;
            color: $gold;

            &:hover, &:focus {
              color: $white;
              background: $red;
            }
          }
        }
      }
    }

    #comment-form {
      .form-wrapper{
        border-bottom: 0px;
      }
      .preview {
        padding: 15px;
        margin-bottom: 30px;

        a {
          &:hover, &:focus {
            color: $gold;
          }
        }
      }
      .form-textarea-wrapper{
        display: inline-block;
        width: calc(100% - 106px);

      }
      .form-item {
        /*margin: 0 0 15px;*/
        margin-bottom: 15px;

        label {
          color: $grey_font_header;
          font-weight: bold;
          min-width: 100px;
          vertical-align: top;
          /*float: right;*/
        }
        .form-text {
          width: calc(100% - 106px);
        }
        .form-text, .form-textarea, .form-select {
          border: 2px solid #e5e4e3;
          border-radius: 3px;
        }
      }

      .form-actions {
        margin: 20px 0 0;
        text-align: center;
      }

      .form-type-textarea {
        margin-bottom: 0;
      }

      .filter-wrapper {
        border-color: #2E2E2E;

        .tips {
          margin-left: 20px;

          li {
            margin-bottom: 7px;
          }
        }
      }

      details {
        background: $white;
        color: $gold;
        border: none;
        margin-bottom: 20px;

        .form-autocomplete {
          background: $white !important;
          color: $gold !important;
          border: 1px solid #ddd !important;
        }

        legend {
          margin-bottom: 10px;
        }

        #edit-keywords-fieldset {
          label {
            min-width: 190px;
          }
        }

        .form-submit {
          margin-bottom: 10px;

          &:hover, &:focus {
            background: $gold;
            color: $white;
          }
        }

        a {
          &:hover {
            color: $gold;
          }
        }

        #edit-date {
          margin-bottom: 10px;
        }
      }
    }
  }

  /* sidebar */
  .sidebar {
    margin-top: 50px;
    h2 {
      font-size: 1.285em;
      text-transform: uppercase;
      color: $white;
      margin-bottom: 30px;
    }

    .block {
      border-top: 2px dotted #555;
      padding-top: 30px;
      margin-bottom: 30px;

      &:first-child {
        border-top: none;
        padding-top: 0;
      }
    }

    .views-row {
      border-top: 1px dotted #555;
      padding-top: 20px;
      margin-bottom: 20px;
      overflow: hidden;

      &:first-child {
        border-top: none;
        padding-top: 0;
      }

      &:last-child {
        margin-bottom: 0;
      }

      .views-field-field-image {
        float: left;
        margin: 5px 15px 5px 0;
      }

      .views-field-title {
        font-size: 1.14em;
        font-weight: bold;
      }
    }

    .item-list {
      ul {
        margin: 0;

        li {
          border-top: 1px dotted #afafaf;
          list-style: none;
          margin: 0;

          a {
            display: block;
            padding: 7px 0;
            color: $light_grey;

            &:before {
              content: "+";
              margin-right: 5px;
            }

            &:hover, &:focus {
              color: $red;
            }
          }

          &:first-child {
            border-top: none;
          }
        }

        li.views-row {
          margin-left: 0;
          list-style: none;
          overflow: visible;
          border-top: 1px dotted #555;
          padding-top: 20px;
          margin-bottom: 20px;

          a {
            padding: 0;

            &:before {
              content: "";
              margin-right: 0;
            }
          }

          &:first-child {
            border-top: none;
            padding-top: 0;
          }

          .views-field-changed {
            font-size: 0.921em;
          }
        }
      }
    }

    /* lgoin */
    #user-login-form {
      .form-item {
        margin: 0 0 10px;

        label {
          display: inline-block;
          min-width: 90px;
        }

        .form-text {
          background: #555;
          border: none;
          padding: 8px;
          width: 250px;
        }
      }

      .form-actions {
        margin: 0 0 10px;
        padding-left: 94px;

        .button {
          margin: 0;
        }
      }
    }

    #user-login-form + .item-list {
      padding-top: 20px;

      ul {
        li {
          border-top: none;
          margin-bottom: 7px;

          a {
            padding: 0;
          }
        }
      }
    }

    .about-me {
      .img {
        float: right;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        border: 4px solid #fff;
        margin: -25px 0 5px 10px;
        max-width: 100px;
      }

      h4 {
        text-transform: uppercase;
        margin-bottom: 5px;
      }
    }

    .month {
      margin-right: 5px;
    }
  }

  ul.inline, ul.links.inline {
    display: block;

  }

  #title {
    background: $gold;
    padding: 30px 0;

    h1 {
      margin-bottom: 0;
      color: $white;
      text-transform: uppercase;
      line-height: 1.4;
    }
  }
}

