#gallery-slider {
  position: relative;

  .slides {
    padding-left: 0;
    margin-left: 0;

    li {
      list-style-type: none;

      img {
        width: 100%;
      }
    }
  }

  .flex-direction-nav {
    padding-left: 0;
    margin: 0;

    li {
      position: absolute;
      left: 0;
      top: 50%;
      z-index: 10;
      list-style: none;
      margin-top: -25px;

      a {
        display: block;
        width: 50px;
        height: 50px;
        background: #000;
        overflow: hidden;
        text-indent: -999em;

        &:before {
          @include fontawesome;

          content: "\f104";
          position: absolute;
          left: 17px;
          top: 7px;
          text-indent: 0;
          font-size: 32px;
          color: #fff;
        }

        &:hover, &:focus {
          background: $red;
        }
      }

      &.flex-nav-next {
        right: 0;
        left: auto;

        a {
          &:before {
            content: "\f105";
            left: 21px;
          }
        }
      }
    }
  }
}

#gallery-carousel {
  .slides {
    padding-left: 0;
    margin: 0;
    padding-top: 20px;

    li {
      list-style-type: none;
      cursor: pointer;
      width: 105px !important;
      margin-right: 14px !important;
      opacity: 0.6;

      &.flex-active-slide {
        opacity: 1;
      }
    }
  }

  .flex-direction-nav {
    display: none;
  }
}

