.path-search {

  #block-women2030-content {
    margin-right: -15px;
    margin-left: -15px;

    h2{ 
      color: #555555;
      font-size: 30px;
      font-weight: bold;
      border-bottom: 1px solid $gold;
      padding: 10px;
    }
    p{
      display: none;
    }

    .node{
      width: 48%;
      display: inline-block;
      margin: 10px;
      position: relative;
      min-height: 1px;
      padding-right: 15px;
      padding-left: 15px;

      .group-mobile{ display: none;}
      .field--name-field-author{display: none;}

      .node--type-article  {
        margin-bottom: 30px;
      }


      .group-header {
        margin-bottom: 30px;

        .field--name-field-image {
          margin-bottom: 10px;   
        }
