        .field--name-body { 
          height: 100px;
          overflow: hidden;

          p {
            display: block;
            text-align: center;
            padding: 0 15px;
            font-size: 15px;
            line-height: 30px;
            font-weight: bold;
          }
        }
      }

      .group-footer {
        border: 1px solid $light_grey;
        border-radius: 4px;
        height: 40px;
        padding: 6px 0;

        .field--name-field-category {
          float: left; /* ltr */
          font-size: 12px;
          color: $grey_font_header;
          text-align: right;
          margin: 0;


          .taxonomy-term  {
            float: right;
            background: $grey_tag_background;
            padding: 6px 10px;
            margin-left: 10px;
            margin-right: 10px;

            h2 {
              margin: 0;
              font-size: 12px;
              font-weight: bold;
              padding: 0;
              border-bottom: 0;

              &:after, &:before {
                content: none;
              }
              a {
                &:hover, &:focus {
                  color: $gold;
                }
                .field--name-name{
                  margin: 0;
                }
              }
            }
            .content { display: none;}
          }
        }

        .field--name-node-post-date {
          float: left; /* ltr */
          font-size: 12px; 
          color: $grey_font_header;
          text-align: right;
          padding-top: 5px;
        }

        .se-block,
        .node__links {
          float: right; /* ltr */
          margin-right: 10px; /* ltr */
          margin-top: 5px;
        }
      }
    }
  } 
